* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "WF Visual Sans Text", sans-serif;
  font-family: "Montserrat";
  transition: all 0.3s ease;
}

body {
  background: linear-gradient(45deg, whitesmoke, white);
}

.alpha-wrapper {
  width: 1440px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding: 1rem;
  padding-top: 0;
}

header {
  color: white;
  font-weight: 500;
  width: 100%;
  padding: 2rem;
  padding-bottom: 10rem;
  position: relative;
  background: url("./../images/bg-pattern-intro-desktop.svg");
  background-size: 200%;
  background-position: center;
  position: relative;
  border-bottom-left-radius: 100px;
}
header::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  opacity: 0.95;
  background: linear-gradient(45deg, #ff8e70, #ff4359);
  z-index: 1;
  border-bottom-left-radius: 100px;
}
header nav, header nav div,
header nav ul {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  z-index: 3;
}
header nav div,
header nav ul {
  gap: 1rem;
}
header nav ul {
  gap: 4rem;
  list-style: none;
  position: relative;
}
header nav ul li {
  cursor: pointer;
}
header nav ul h1 {
  cursor: pointer;
}
header .login {
  cursor: pointer;
}
header .sign-up {
  background: white;
  padding: 0.8rem;
  border-radius: 50px;
  color: #ff4359;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease-in;
}
header .sign-up:hover {
  background: rgba(225, 225, 225, 0.5);
  color: white;
}

.connect-after-div {
  opacity: 0;
  position: absolute;
  right: -2rem;
  top: 2rem;
  border-radius: 0.5rem;
  display: flex;
  flex-direction: column;
  font-size: 0.7rem;
  padding-left: 1rem;
  gap: 0.05rem;
  align-items: flex-start;
  justify-content: space-around;
  width: 7rem;
  height: 7rem;
  background: white;
  color: black;
  font-family: "Times New Roman", Times, serif;
  box-shadow: 0px 10px 20px 0px rgba(0, 0, 0, 0.37);
}
.connect-after-div ul {
  list-style: none;
  display: block;
  padding: 0.2rem;
}

.section-0 {
  color: white;
  position: relative;
  z-index: 50;
  margin-top: 5rem;
}
.section-0 h1 {
  color: white;
  z-index: 1000;
  font-size: 2rem;
  text-align: center;
  margin-top: 3rem;
}
.section-0 p {
  text-align: center;
}

.section-0-btn-div {
  margin-top: 1rem;
  width: 18%;
  height: 4rem;
  display: block;
  margin-right: auto;
  margin-left: auto;
  align-items: center;
  justify-content: space-around;
}

.section-0-wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
  gap: 0.5rem;
}

.section-0-btn-1 {
  width: 7rem;
  height: 2.5rem;
  background: white;
  border-radius: 500000px;
  display: grid;
  place-content: center;
  color: #ff4359;
}
.section-0-btn-1:hover {
  background: rgba(225, 225, 225, 0.5);
  color: white;
}
.section-0-btn-1 p {
  font-weight: 700;
  font-size: 0.7rem;
}

.section-0-btn-2 {
  width: 7rem;
  height: 2.5rem;
  border-radius: 500000px;
  border: 2px solid white;
  display: grid;
  place-content: center;
  cursor: pointer;
  color: white;
}
.section-0-btn-2:hover {
  background: white;
  color: #ff4359;
}
.section-0-btn-2 p {
  font-size: 0.7rem;
  font-weight: 500;
}

.section-2 .section-2-h1 {
  text-align: center;
  margin-top: 10rem;
}

.section-2-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  margin-top: -5rem;
}
.section-2-container .section-2-img-container,
.section-2-container .section-2-txt-container {
  width: 50%;
  height: 100%;
}
.section-2-container .section-2-txt-container {
  line-height: 2rem;
}

.section-3 {
  margin-top: 5rem;
  background: #3b3e5f;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  color: white;
  line-height: 2rem;
  height: 25rem;
  border-bottom-left-radius: 70px;
  border-top-right-radius: 70px;
}

.section-3-txt-container {
  width: 50%;
}

.section-4 {
  margin-top: 5rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
.section-4 img {
  height: 40rem;
  margin-left: -10rem;
}
.section-4 div {
  width: 50%;
  height: 100%;
  line-height: 1.5rem;
}

footer {
  margin-top: 5rem;
  border-top-right-radius: 100px;
  bottom: 0;
  width: 100%;
  height: 25rem;
  background: #24242c;
  padding: 3rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
footer table {
  table-layout: fixed;
  border-collapse: collapse;
  padding: 1rem;
  text-align: left;
  color: #e2e3e5;
}
footer table td, footer table th {
  padding: 1rem;
  padding-left: 5rem;
  padding-right: 5rem;
  cursor: pointer;
  width: 4rem;
}
footer table td:hover, footer table th:hover {
  text-decoration: underline;
}

.footer-wrapper {
  width: 80%;
  display: flex;
  flex-direction: row;
}
.footer-wrapper div {
  width: 30%;
}

.attribution {
  display: block;
  margin-right: auto;
  margin-left: auto;
  text-align: center;
  background: #24242c;
}